import request from '../utils/axios'

export const getSpeechList = async (params) => {
  return request({
    url: '/tts/model/list',
    method: 'get',
    params
  })
}

export const getCategories = async (params) => {
  return request({
    url: '/tts/model/categories',
    method: 'get',
    params
  })
}

export const postUpdateModel = async (data) => {
  return request({
    url: `/tts/model/${data.sound_model_id}/update`,
    method: 'post',
    data: data
  })
}

export const postGenerate = async (data) => {
  return request({
    url: `/tts/generate`,
    method: 'post',
    data: data
  })
}

export const getModel = (soundId) => {
  return request({
    url: `/tts/model/${soundId}`,
    method: 'get'
  })
}

export const postAddModel = (data) => {
  return request({
    url: `/tts/model`,
    method: 'post',
    data: data
  })
}

export const postUploadFile = (data) => {
  return request({
    headers: { 'Content-Type': 'multipart/form-data' },
    url: `/file/upload`,
    method: 'post',
    data: data
  })
}

// 获取模型列表
export const getModelList = (params) => {
  return request({
    url: '/tts/model/list/admin',
    method: 'get',
    params
  })
}

// 删除模型
export const PostDeleteModel = ({ soundId }) => {
  return request({
    url: `/tts/model/${soundId}/delete`,
    method: 'post'
  })
}

export const getTaskDetail = () => {
  return request({
    url: `/tts/training/task/detail`,
    method: 'get',
  })
}

